$tc : #d5bf04;
* {
	margin:0;
	padding:0;
	list-style: none;
}
h1,h2,h3,h4,h5,h6,a, p{
	font-family: 'Raleway', sans-serif;
}
a {
	text-decoration:none;	
}

.top-bar {
	color:white;
	background-color:black;
	width:100%;
    padding: 10px 0;
	.top-bar i {
		color:white;
	}
	.top-fonts {
		float:left;
		ul li {
			display: inline;
			i {
				margin-right: 15px;
			}
		}
	}
	.top-right {
		float:right;
	}
}
.top-right h5{
	margin-left: 15px;
	display: inline-block;
	font-weight:300;
	i{
		margin-right: 10px;
	}
}
header{
	margin-bottom:0px;
}

.logo {
	float:left;
	padding-top: 10px;
	img {
		width:200px;
		vertical-align: middle;
	}
}
nav {
	float:right;
	font-family:arial;
	padding: 27px 0;
	ul li {
		display:inline;
	 	a {
	 		font-size:14px;
			color: #1b1919;
			text-decoration:none;
			padding:7px 10px;
			border-radius: 1px;
			transition:all.4s ease;
			background-color:white;
		}
		a:hover {
			background-color:yellow;
		}
	}
}
.bg-image{
	font-family:arial;
	background-image: url('../img/home-without-effect.jpg');
	background-size:cover;
	padding-top:180px;
	.bg-inner {
		h1 {
			font-size:40px;
			font-weight: 400;
			span{
				color:#ffe400;
				background-color:black;
			}	
		}
		h1:last-child {
			font-weight:lighter;
			margin:0;
		}
		a{
			border:1px solid #706f6f;
			border-radius:20px;
			padding:9px 15px;
			color:#706f6f;
			font-size:12px;
			margin:30px 0 71px 0;
			display:inline-block;
		}
		p{
			font-size:16px;
			line-height:20px;
			color:#706f6f;
		}
	}
}
.main {
	text-align:center;
	font-family:arial;
	margin-top:100px;
	margin-bottom:50px;
	h2 {
		font-weight:bold;
		font-size: 32px;
		span {
			color:#888888;
		}
	}
	img {
		height:100px;
	}
	h4 {
		font-size:23px;
		font-weight:lighter;
	}
	h3 {
		    font-size: 16px;
		    color: #353333;
		    line-height: 20px;
		    font-weight: 400;
		    letter-spacing: 2px;
	}
	p {
		color:#969595;
	    font-size: 12px;
	    line-height: 20px;
	}
}
.one-third {
	width:33%;
	text-align:center;
	float:left;
	h4 {
		font-weight:lighter;
		margin:10px 0;
		color:#454343;
	    font-weight: 500;
	    font-size: 16px;
		letter-spacing:4px;
	}
	p {
		color:#9a9a9a;
		margin-bottom:20px;
		font-size: 12px;
	    line-height: 20px;

	}
	a {
		border-bottom:4px solid #ffe400;
		margin-bottom:20px;
		padding-bottom:5px;
		color: #736e6e;
	    letter-spacing: 1px;
	    font-size: 12px;
	}
}
.clearfix:after {
		content:'';
		display:table;
		clear:both;
}
.bg-color {
	background-color:#333333;
}
.bg-color:after{
	content:'';
	display:table;
	clear:both;
}
.one-forth{
	width:25%;
	float:left;
}
.project{
	text-align:center;
	color:#bcbcbc;
	padding:40px 0;
	h1 {
		font-size:55px;
		font-weight:lighter;
	}
}
footer {
	background-color:#1b1919;
	color:#bcbcbc;
	width:100%;
	float:left;
	padding:70px 0;
}
.one-forth {
	h4 {
		font-size:20px;
		padding-bottom:20px;
	}
	p {
	 font-size:12px;
	}
	a {
		color:#bcbcbc;
	}
}
.footer-inner {
	.mg-left{
		margin-left:25px;
	}
	.last-icon {
		i{
			display:inline-block;
			float:left;
			width:35px;
			line-height:35px;
			text-align:center;
			margin:15px 5px 0 0;
			height:35px;
			border-radius:50%;
			border:1px solid;
		}
		i:hover{
			color:$tc;
			cursor:pointer;
		}
	}
	input {
		width:95%;
		border:1px solid #2b2a2a;
		background-color:#2b2a2a;
		margin-bottom:10px;
		padding:5px 0 5px 5px;
		height:30px;
	}
	button {
		width:120px;
		height:45px;
		border-radius:24px;
		color:$tc;
		border:1px solid $tc;
		background-color:#1b1919;
	}
	button:hover {
			background-color:$tc;
			color:#1b1919;
			border:2px solid #1b1919;
	}
	p {
		margin-bottom:15px;
		i{
			margin-left:5px;
			color:$tc;

		}
		span {
			margin-left:10px;
		}
		span.mg {
			margin-left:25px;
		}
	}

}




@media only screen and (max-width: 1199px){
	.container {
	    width: 970px;
	}
}

@media only screen and (max-width: 991px){
 .container {
    width: 750px;
 }
}
@media only screen and (min-width: 1200px){
	.container {
	    width: 1170px;
	}
}

.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}



@media only screen and (max-width:768px){
	.top-bar{
		background-color: blue;
	}
	.top-fonts{
		float:none;
	}
	.top-right{
		float:none;
	}
}